/* Estilos globales */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
  color: #333;
  margin: 5%;
  margin-top: 1%;
}

h1 {
  text-align: center;
  font-size: 3rem; /* Tamaño de fuente grande */
  font-family: 'Roboto', sans-serif; /* Fuente moderna */
  margin-top: 30px; /* Mayor espacio superior */
  margin-bottom: 30px; /* Espacio inferior */
  color: #a2ff00; /* Color azul */
  text-transform: uppercase; /* Mayúsculas */
  font-weight: bold; /* Negrita */
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5); /* Sombra de texto más pronunciada */
}

h2 {
  text-align: center;
  font-size: 1.5rem; /* Tamaño de fuente */
  font-family: 'Roboto', sans-serif; /* Fuente moderna */
  margin-top: 1px; /* Espacio superior */
  margin-bottom: 20px; /* Espacio inferior */
  color: #555; /* Color de texto gris */
}

.signature {
  font-style: italic; /* Cursiva */
  font-weight: normal; /* Normal (no negrita) */
  text-transform: capitalize; /* Capitalizar primera letra de cada palabra */
}
/* Contenedor principal */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-top: 40px;
}

/* Estilo para cada pregunta */
.question {
  margin-bottom: 30px;
  padding: 20px;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  background-color: #fdfdfe;
}

.question p {
  font-weight: bold;
  margin-bottom: 15px;
}

.question.incorrect {
  border-color: #ff4d4d;
  background-color: #ffe6e6;
}

.options div {
  margin-bottom: 10px;
}

input[type="radio"] {
  margin-right: 10px;
}

label {
  cursor: pointer;
}

/* Mensaje de selección */
.selected-message {
  margin-top: 10px;
  font-style: italic;
  color: #007bff;
}

/* Feedback de respuestas */
.feedback {
  margin-top: 10px;
  font-size: 24px;
}

/* Botón de mostrar respuesta */
button {
  display: block;
  margin-top: 10px;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Estilo para la puntuación */
.score {
  text-align: center;
  font-size: 24px;
  margin-top: 20px;
  color: #007bff;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  background-color: #fdfdfe;
  width: 20%;
}
.dark-mode body {
  background-color: #343a40;
  color: #dee2e6;
}

.dark-mode .container {
  background-color: #454d55;
}

.dark-mode .question {
  background-color: #495057;
}

.dark-mode .question p {
  color: #fff;
}

.dark-mode .question.incorrect {
  background-color: #f8d7da;
}

.dark-mode .options div label {
  color: #fff;
}

.dark-mode .selected-message {
  color: #a2ff00;
}

.dark-mode .feedback {
  color: #a2ff00;
}

.dark-mode button {
  background-color: #007bff;
}

.dark-mode button:hover {
  background-color: #0056b3;
}

.dark-mode .score {
  color: #a2ff00;
  background-color: #495057;
}